<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="insulpanel.meta_semana"
    :progress="insulpanel.progress"
    :name="insulpanel.nombre"
    :depto="insulpanel.depto"
    :days="insulpanel.dias_laborados"
    :m3="insulpanel.m3_persona"
    :bonus_p="insulpanel.bono_persona"
    :bonus_tot="insulpanel.bono_depto"
    :asis="insulpanel.asistencia"
    :extra="insulpanel.$_extra_m3"
    :bonus_prod="insulpanel.bono_productividad"
    :bonus_goals="insulpanel.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Insulpanel',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'insulpanel',
            userCode: this.$route.params.user,
            city: 'guadalajara'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        insulpanel() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>